*{
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
}
body
{
    display:flex;
    justify-content: center;
    align-items: center;
}
.con
{
    top:50%;
    left:50%;
    position: absolute;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    width:256px;
    text-align: center;
    border: 10px solid black;
    background: black;
    border-radius: 6px;
}
.keypad
{
    display:grid;
    grid-template-columns:repeat(4,1fr);
    grid-auto-rows:minmax(60px,auto);

}
input[type="text"]
{
    height: 120px;
    width: 251px;
    background-color:rgb(147, 126, 156);
    border: 0px;
    color:#fff;
    text-align:right;
    font-weight: bolder;
    font-size: x-large;
    letter-spacing: 1px;
}
#clear
{
    grid-column: 1/3;
    grid-row: 1;
}
#result
{
    grid-column: 3/5;
    grid-row: 5;
}
button
{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: rgb(27, 25, 25);
    color: #fff;
    font-weight: 400px;
}

